import { Dispatch } from "react";
import { ActionType } from "../common/resources/redux/types";
import xhttp from "../common/utils/Network";
import getConfig from "./../common/appConfig";

export function getRequestAction() {
    return async function(dispatch: Dispatch<ActionType>) {
        const input = {
            // url : getConfig("host"),
            url : getConfig("host")+"dashboard",
            method : "GET",
            // headers : {
            //     "Authorization": "Bearer "+token,
            // }
          }
          const resp : any = await xhttp(input);
          
          dispatch({
            type: "GET_REQUEST",
            payload: resp.data
          });
    }
}

export function getSearchRequestAction(value:any) {
    return async function(dispatch: Dispatch<ActionType>) {
        const input = {
            url : getConfig("host")+"dashboard"+"?search="+value,
            method : "GET",
            // headers : {
            //   "Authorization": "Bearer "+token,
            // }
          }
          const resp : any = await xhttp(input, {
            cancelPrevious: true,
            requestType: "SearchCampaign"
          });
          
          dispatch({
            type: "GET_REQUEST",
            payload: resp.data
          });
    }
}

export function getFilterdDataAction(type :any,booked:any,priority:any,startDate:any,endDate:any) {
  let startdate = startDate
  let enddate = endDate

  if(startdate && startdate[0].length === 0 ){
    startdate = ""
  }
  else if(enddate && enddate[0].length === 0 || enddate === ","){
    enddate = ""
  }
  return async function(dispatch: Dispatch<ActionType>) {
      const input = {
          url : getConfig("host")+"dashboard"+"?type="+type+"&priorityLevel="+priority+"&bookedStatus="+booked+"&startDate="+startdate+"&endDate="+enddate,
          method : "GET",
          // headers : {
          //   "Authorization": "Bearer "+token,
          // }
        }
        const resp : any = await xhttp(input);
        
        dispatch({
          type: "GET_REQUEST",
          payload: resp.data
        });
  }
}

