import React from 'react'
import './calender.css'
import front from './../../../../../../img/right-arrow.svg'
import back from './../../../../../../img/left-arrow.svg'
import { CalenderStoreProps } from "./calender.intrfaces"

class Calender extends React.Component<CalenderStoreProps>{

	state = {
		Days : [
			"Mon",
			"Tue",
			"Wed",
			"Thu",
			"Fri",
			"Sat",
			"Sun"
		],
		Month : [
			"January",
			"February",
			"March",
			"April",
			"May",
			"June",
			"July",
			"August",
			"September",
			"October",
			"November",
			"December"
		],
		style : this.props.style !== undefined ? this.props.style  : {},
		currentMonth : null,
		currentYear : null,
		calenderData : [],
		width : this.props.width ? this.props.width : "240px",
		marginTop : this.props.marginTop !== undefined ? this.props.marginTop : "-7%",
		recurring : this.props.recurring ? this.props.recurring : "",
		id : "month_swap"
	}
	
	componentDidMount(){
		var today = new Date();
		var currentMonth:any = today.getMonth() + 1;
		currentMonth = currentMonth.toString().length === 1 ? "0"+currentMonth : currentMonth.toString();
		var currentYear = today.getFullYear();
		var dateOnFirst = new Date(currentYear+"/"+currentMonth+"/01");
		var dayOnFirst = dateOnFirst.getDay();
		console.log("jjfi",dateOnFirst,dayOnFirst)
		var daysInMonth = this.daysInMonth(currentMonth,currentYear);
		console.log("month",daysInMonth)
		var calenderData = this.createCalenderData(dayOnFirst,daysInMonth+1);
		
		this.setState({
			currentMonth : currentMonth,
			currentYear :currentYear,
			calenderData : calenderData
		},() => {
			console.log(this.state.currentMonth,this.state.currentYear,this.state.calenderData)
		})	
	}

	componentWillReceiveProps(nextProps:any){
		if(this.props.marginTop !== nextProps.marginTop){
			this.setState({
				marginTop : nextProps.marginTop
			})
		}

		if(this.props.recurring !== nextProps.recurring){
			this.setState({
				recurring : nextProps.recurring
			})
		}

		if(this.props.style !== nextProps.style){
			this.setState({
				style : nextProps.style
			})
		}
	}

	daysInMonth(month:any, year:any) {
    	return new Date(year, month, 0).getDate();
	}

	createCalenderData(dayOnFirst:any, daysInMonth:any){
		var counter = 0;
		var arr = [];
		for(var i = 1 ; i <= 42 ; i++){
			if(i < dayOnFirst || i >= dayOnFirst+daysInMonth)
				arr.push(".")
			else{
				counter = counter+1
				arr.push(counter)
			}	
		}

		return arr;
	}

	previousMonth(e:any){
		console.log("iddd", e.target.id)
		this.props.calenderId(e.target.id);
		var month:any = this.state.currentMonth
		var currentMonth:any = parseInt(month) - 1;
		var currentYear:any = this.state.currentYear;
		
		if(currentMonth === 0){
			currentMonth = 12
			currentYear = currentYear - 1
		}

		currentMonth = currentMonth.toString().length === 1 ? "0"+currentMonth : currentMonth.toString();

		var dateOnFirst = new Date(currentYear+"/"+currentMonth+"/01");
		var dayOnFirst = dateOnFirst.getDay();
		var daysInMonth = this.daysInMonth(currentMonth,currentYear);
		var calenderData = this.createCalenderData(dayOnFirst,daysInMonth);
		
		this.setState({
			currentMonth : currentMonth,
			currentYear : currentYear,
			calenderData : calenderData
		})
	}

	nextMonth(e:any){
		this.props.calenderId(e.target.id)
		// var thisDate = new Date();
		// var thisYear = thisDate.getFullYear();
		// var thisMonth = thisDate.getMonth()
		var month:any = this.state.currentMonth
		var currentMonth:any = parseInt(month)+1;
		var currentYear:any = this.state.currentYear;
		
		if(currentMonth === 13){
			currentMonth = 1
			currentYear = currentYear+1
		}

		currentMonth = currentMonth.toString().length === 1 ? "0"+currentMonth : currentMonth.toString();

		var dateOnFirst = new Date(currentYear+"/"+currentMonth+"/01");
		var dayOnFirst = dateOnFirst.getDay();
		var daysInMonth = this.daysInMonth(currentMonth,currentYear);
		var calenderData = this.createCalenderData(dayOnFirst,daysInMonth);
		this.setState({
			currentMonth : currentMonth,
			currentYear : currentYear,
			calenderData : calenderData
		})
	}

	clickDate(receive:any){
		receive = receive.toString().length === 1 ? "0"+receive : receive.toString();
		var currentYear = this.state.currentYear;
		var currentMonth = this.state.currentMonth;
		var currentDate = receive+"-"+currentMonth+"-"+currentYear;
        this.props.onSelect(currentDate)
	}

	render(){
		var style = {
			marginTop : this.state.marginTop,
			width : this.state.width,
			zIndex : "99999",
			...this.state.style
		}
		var currentMonth:any = this.state.currentMonth
		var currentYear:any = this.state.currentYear
		return(
			<div className="wrapper" style={style}>
				<div className ="header-wrapper"> 
					<div className="header">
						<div className="previous-month" id = {this.state.id} onClick={this.previousMonth.bind(this)} >
							<img id = {this.state.id} src={back} alt="" style={{width:"10px",height:"10px"}}/>
						</div>
						<div className="display-month"><b>{this.state.Month[currentMonth - 1]+", "+this.state.currentYear}</b></div>
						<div className="next-month" id = {this.state.id} onClick={this.nextMonth.bind(this)}>
							<img id = {this.state.id} src={front} alt="" style={{width:"10px",height:"10px"}} />
						</div>
					</div>
				</div>
				<div className="days-row">
					{
						this.state.Days.map(function(value,index){
							return(
								<div className="day-cell" key = {"cal"+index}>{value}</div>
							)
						})
					}
				</div>
				<div className="date-wrapper">
					{
						this.state.calenderData.map((value:any,index:any) => {
							if(value !== "."){
								var day = value.toString().length === 1 ? "0"+value : value;
								var optDate = new Date(currentYear+"/"+currentMonth+"/"+day);
							
								var now = new Date();
								now.setDate(now.getDate() - 1);
								
								var style = {}
								// if(this.state.recurring === "true"){
								// 	if(optDate.getTime() < now.getTime()){
								// 		style = {
								// 			pointerEvents : "none",
								// 			color : "#D7D7D7"
								// 		}
								// 	}
								// }
								// else{
								// 	var newNow = new Date();
								// 	if(optDate.getTime() > newNow.getTime()){
								// 		style = {
								// 			pointerEvents : "none",
								// 			color : "#D7D7D7"
								// 		}
								// 	}
								// }
								return(
									<div className="date-cell" style = {style} onClick={this.clickDate.bind(this,value)} key = {"so"+index}>
										{value}
									</div>
								)
							}
							else{
								return(
									<div className="date-cell" key = {"data"+index} style = {{
										pointerEvents : "none",
										color : "#FFFFFF",
									}} onClick={this.clickDate.bind(this,value)}>
										{value}
									</div>
								)
							}
						})
					}
					
				</div>
			</div>	
		)
	}
}
export default Calender;