// import { Dictionary } from "../common/resources/lang/object/Dictionary";
import { ActionType } from "../common/resources/redux/types";

interface ReducerState {
  requestData : object;
  bookingDetail : object;
  requestId : object;
  subCamRequestId : object;
  addID : object;
  broadCastData : object;
  refresh : object;
  delete : object;
  discoveryRequest: object;
  forecastData: object;
  token: object;
}
  
export default function reducer(
  state: ReducerState = {
    requestData : {},
    bookingDetail :{},
    requestId : {},
    subCamRequestId : {},
    addID : {},
    broadCastData : {},
    refresh : {},
    delete : {},
    discoveryRequest: {},
    forecastData: {},
    token: {}
  },
  action: ActionType
): ReducerState {
  switch (action.type) {
    case "GET_REQUEST" : {
      return { ...state, requestData : action.payload }
    }
    case "GET_BOOKING_DETAIL" : {
      return { ...state, bookingDetail : action.payload }
    }
    case "GET_REQUEST_ID" : {
      return { ...state, requestId : action.payload }
    }
    case "GET_SUB_CAM_REQUEST_ID" : {
      return { ...state, subCamRequestId : action.payload }
    }
    case "SEARCH_ADD_ID" : {
      return{ ...state, addID : action.payload }
    }
    case "BROAD_CAST" : {
      return { ...state, broadCastData : action.payload }
    }
    case "REFRESH" : {
      return { ...state, refresh : action.payload }
    }
    case "DELETE_SUB_CAMPAIGN" : {
      return { ...state, delete : action.payload }
    }
    case "DISCOVERY_REQUEST" : {
      return { ...state, discoveryRequest: action.payload}
    }
    case "FORECAST_DATA" : {
      return { ...state, forecastData: action.payload }
    }
    case "GET_TOKEN" : {
      return { ...state, token: action.payload }
    }
  }
  return state;
}
  