import React from "react";
import styled from "styled-components";
import left from "./../../../../../../img/group-7.svg";
import right from "./../../../../../../img/group-8.svg";

// interfaces
import { ScrollStoreProps, ScrollProps} from"./horizontalScroll.interfaces";

export default class HorizontalScrollableStack extends React.Component<ScrollStoreProps> {
  private horizontalStackWrapperRef = React.createRef<HTMLDivElement>();
  state = {
    scrollParams: {
      left: 0,
      leftButton: true,
      rightButton: true,
    },
    item : this.props.item ? this.props.item : {
      name: "",
      key: 0
    },
    arr : this.props.arr ? this.props.arr : [""],
  };

  componentDidMount = () => {
    if (this.horizontalStackWrapperRef.current) {
      this.horizontalStackWrapperRef.current.addEventListener(
        "mousewheel",
        e => {
          e.preventDefault();
          return false;
        }
      );
    }
  };

  componentWillUnmount = () => {
    if (this.horizontalStackWrapperRef.current) {
      this.horizontalStackWrapperRef.current.removeEventListener(
        "mousewheel",
        e => {
          e.preventDefault();
          return false;
        }
      );
    }
  };

  componentDidUpdate = (prevProps: any) => {
    if(this.props.item !== prevProps.item) {
      this.setState({
        item : this.props.item
      })
    }

    if(this.props.arr !== prevProps.arr) {
      this.setState({
        arr : this.props.arr
      })
    }
  }

  arr = () => {
    // const arr = [];
    // for (let i = 0; i < 100; i++) {
    //   arr.push(i);
    // }

    // return arr;
    
  };

  moveScroll = (type: string) => {
    const containerWidth = 300;
    if (
      this.horizontalStackWrapperRef &&
      this.horizontalStackWrapperRef.current
    ) {
      const scrollable = this.horizontalStackWrapperRef.current;
      switch (type) {
        case "right": {
          const left = this.state.scrollParams.left + containerWidth;
          if (scrollable.scrollLeft + containerWidth === left) {
            scrollable.scrollLeft = left;
            this.setState({
              scrollParams: {
                ...this.state.scrollParams,
                left,
                rightButton: true
              }
            });
          } else {
            this.setState({
              scrollParams: {
                ...this.state.scrollParams,
                leftButton: false
              }
            });
          }

          break;
        }
        case "left": {
          const left = this.state.scrollParams.left - containerWidth;
          if (scrollable.scrollLeft > 0) {
            scrollable.scrollLeft = left;
            this.setState({
              scrollParams: {
                ...this.state.scrollParams,
                left,
                leftButton: true
              }
            });
          } else {
            this.setState({
              scrollParams: {
                ...this.state.scrollParams,
                rightButton: false
              }
            });
          }
          break;
        }
      }
    }
  };

  subCampaignDetail = (items:any) => {
    const { arr } = this.state;
    this.props.subCampaignDetails(items)
    this.setState({
      item : items
    })
  }

  renderStackItems = () => {
    var activeState = {
      background : "#979797",
      color : "#f6f6f6"
    }
    var normalState = {
      background : "#f6f6f6",
      color : "#616161"
    }
    const { arr } = this.state;
    return arr.map((items:any, k:any) => {
      return <ItemWrapper style = {this.state.item.key === k ? {backgroundColor:"#979797",color:"#f6f6f6",minWidth:"150px",width:"100%"} : {backgroundColor:"#f6f6f6",color:"#616161",minWidth:"150px",width:"100%"}} key={"scrollable_" + k} onClick = {this.subCampaignDetail.bind(this, items)}>{items.name.length === 0 ? "SubCampaign "+(k+1) : items.name}</ItemWrapper>;
    });
  };
  render() {
    return (
      <>
        <HorizontalStackContainer>
          <HorizontalStackCell>
            {/* {this.state.scrollParams.leftButton ? ( */}
              <div style = {{cursor:"pointer"}} onClick={this.moveScroll.bind(this, "left")}><img src  = {left} alt ="" /></div>
            {/* ) : null} */}
          </HorizontalStackCell>
          <HorizontalStackCell>
            <HorizontalStackWrapper ref={this.horizontalStackWrapperRef} width = {this.props.width}>
              {this.renderStackItems()}
            </HorizontalStackWrapper>
          </HorizontalStackCell>
          <HorizontalStackCell>
            {/* {this.state.scrollParams.rightButton ? ( */}
              <div style = {{cursor:"pointer"}} onClick={this.moveScroll.bind(this, "right")}><img src  = {right} alt ="" /></div>
            {/* ) : null} */}
          </HorizontalStackCell>
        </HorizontalStackContainer>
      </>
    );
  }
}

const HorizontalStackContainer = styled.div`
  display: table;
  width : 100%;
  overflow-x: auto;
`;

const HorizontalStackCell = styled.div`
  display: table-cell;
  overflow-x: auto;
  // vertical-align: top;
  // padding-top: 10px;
`;

const HorizontalStackWrapper = styled.div<any>`
  display: flex;
  // flex-direction: row;
  // justify-content: flex-start;
  max-width: ${(props) => props.width ? props.width : "370px"};
  min-width:313px;
  // height: 200px;
  overflow-x: hidden;
  // background-color: cyan;
`;

const ItemWrapper = styled.div`
  min-width: 150px;
  height: 40px;
  border-radius: 9px 9px 0px 0px;
  background-color: #f6f6f6;
  cursor:pointer;
  font-family: Helvetica;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #616161;
  box-sizing: border-box;
  padding-top: 10px;
`;