
if(process.env.NODE_ENV === "development"){
    var config = require("./../../commom/development.json"); 
}
else{
    var config = require("./../../commom/development.json"); 
}
const constants: any = config;

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export default function getConfig(key: any): any {
    if(!constants) return
    if (key.indexOf(":") !== -1) {
        let arr = key.split(":");
        let value: any = null;
        arr.map((v: any, k: number) => {
            if(k === 0)
                value = constants[v]
            else    
                value = value[v];
        })

        return value;
    } else {
        return constants[key]
    }
}
