import React from "react";
import {BookingStoreProps, BookingProps} from "./newBooking.interfaces";
import { StoreType, ReduxPropsType } from "./../../../common/resources/redux/types";
import { connect } from "./../../../common/ReduxDecorator";
import active from "./../../../../img/active.svg";
import logout from "./../../../../img/log-out.svg";
import logo from "./../../../../img/talpa-logo.svg";
import "./index.css";
import Dropdown from "./../../core/components/Master/Dropdown"
import Input from "../../core/components/TestComponent/components/Input";
import Button from "./../../core/components/TestComponent/components/Button";
import { Modal } from 'antd';
import Scroll from "./../../core/components/Master/HorizontalScrollableStack";
import { requestIDAction, discoveryAction, BookCampaignAction, subCampaignRequestIDAction,
    searchAddIdAction, broadCastDataAction, refreshAction, saveAction, getForecastDataAction 
} from "./../../../actions/dashboardAction"
import moment from "moment"
import addIcon from "./../../../../img/add.svg" ;
import inActiveaddicon from "./../../../../img/group-5.svg"
import deleteActive from "./../../../../img/group-3.svg";
import deleteInactive from "./../../../../img/group-4.svg";
import refreshicon from "./../../../../img/refresh.svg";
import inActiverefreshicon from "./../../../../img/inActiveRefresh.svg";
import back from "./../../../../img/arrow.svg"
import swal from 'sweetalert';
import HighchartsReact from 'highcharts-react-official';
import VennModule from "highcharts/modules/venn.js";
import Highcharts from 'highcharts';
import HC_more from 'highcharts/highcharts-more'; //module
HC_more(Highcharts) //init module

VennModule(Highcharts);//init module

@connect(
    (store:StoreType):BookingStoreProps => {
        if(!store) return ({} as unknown) as BookingStoreProps;

        return {
            requestId : store.dashboard.requestId,
            subCamRequestId : store.dashboard.subCamRequestId,
            addID : store.dashboard.addID,
            broadCastData : store.dashboard.broadCastData,
            refresh : store.dashboard.refresh,
            discoveryRequest: store.dashboard.discoveryRequest,
            forecastData: store.dashboard.forecastData
        }
    }
)

export default class BookingModule extends React.Component<BookingProps>{
    state:any = {
        icon : true,
        bookCampaign : false,
        visible : false,
        fieldsArr : [],
        item: {
            name: "",
            key: 0
        },
        dropdownTypeList  : ["Gender","Age Range","Age of Oldest Child","Geotargeting","Law Restrictions","Household Income"],
        genderList : ["Male","Female","Other"],
        age :[ 
            "18-34 years old",
            "35-54 years old",
            "55-67 years old",
            "68+ years old"
        ],
        ageOfOldest : [
            "0-5 years old",
            "6-12 years old",
            "13-18 years old",
            "19-24 years old"
        ],
        geoTargettingList : [
            "North Holland",
            "South Holland",
            "Utrecht",
            "Friesland",
            "Drenthe",
            "North Brabant",
            "Overijssel",
            "Flevoland",
            "Gelderland",
            "Groningen",
            "Limburg",
            "Zeeland"
        ],
        LawRestrictionsList : [
            "Gambling: Not before 19:00hrs",
            "Alcohol: Not before 21:00hrs",
            "12+ Age Restriction: Not before 20:00hrs",
            "16+ Age Restriction: Not Before 22:00hrs"
        ],
        houseHoldList : [
            "Below Average",
            "Average",
            "1.5 times Average",
            "Twice the Average",
            "More than Twice the Average"

        ],
        priorityList : [
            "High",
            "Low",
            "Medium"
        ],
        discoveryrequest : {
            campaignID : "null", 
            campaignName : "null",
            campaignDate : "null",
            subCampaign : [
                {
                    requestID : "null",
                    subCampaignName: "",
                    adID : "null",
                    subCampaignDate : "null",
                    audienceTargeting : []
                } 
           ],
           priorityLevel : "null",
           CPM : "null",
           budget : "null",
           impressions : "null"
        },
        activeIndex : {
            index : 0,
            data : {
                requestID : "null",
                subCampaignName: "",
                adID : "null",
                subCampaignDate : "null",
                audienceTargeting : []
            }
        },
        searchList : [],
        searchDisplay : false,
        broadCastArr : [],
        forecastData : {},
        graphValue : 0,
        timeStamp : ""
    }

    componentDidMount(){
        if(this.props.location.state && this.props.location.state.hasOwnProperty('data')){
            // console.log("initial value ,", this.props.location.state.data);
            this.setState({
                discoveryrequest : {
                    ...this.state.discoveryrequest,
                    ...this.props.location.state.data
                },
                activeIndex : {
                    index : 0,
                    data : this.props.location.state.data.subCampaign[0]
                },
                item : {
                    name: this.props.location.state.data.subCampaign[0].subCampaignName,
                    key: 0
                }
            }, () => {
                this.props.dispatch(broadCastDataAction(this.props.location.state.data.campaignID));

                // console.log("later --- ",this.state.discoveryrequest)
            })
        }
        else {
            this.props.dispatch(requestIDAction());
            this.props.dispatch(subCampaignRequestIDAction());
        }
    }

    componentWillReceiveProps(nextProps:any){
        if(this.props.requestId !== nextProps.requestId){
            var discoveryRequest = Object.assign({},this.state.discoveryrequest);
            discoveryRequest.campaignID = nextProps.requestId.requestID;
            this.setState({
                discoveryrequest : discoveryRequest,
            })
            this.props.dispatch(broadCastDataAction(nextProps.requestId.requestID));
        };

        if(this.props.subCamRequestId !== nextProps.subCamRequestId){
            const index = this.state.activeIndex.index;
            const {discoveryrequest} = this.state;
            const campaign = discoveryrequest.subCampaign[discoveryrequest.subCampaign.length - 1];
            campaign.requestID = nextProps.subCamRequestId.requestID
            this.setState({
                discoveryrequest : Object.assign({}, discoveryrequest),
                activeIndex: {
                    index: 0,
                    data: this.state.discoveryrequest.subCampaign[0],
                }
            })
        };

        if(this.props.addID !== nextProps.addID){
            this.setState({
                searchList : nextProps.addID,
            })
        };

        if(this.props.broadCastData !== nextProps.broadCastData){
            if(nextProps.broadCastData.length !== 0){
                const output = JSON.parse(nextProps.broadCastData[0].Discover);
                const { targets } = output;
                const pool = targets[0].totalHouseholdPoolsize;
                // console.log("mm",nextProps.broadCastData)
                this.setState({
                    broadCastArr : nextProps.broadCastData,
                    graphValue : pool,
                    timeStamp : nextProps.broadCastData[0].Timestamp,
                    forecastData : nextProps.broadCastData[0]
                },() => {
                    // console.log("pp",this.state.timeStamp)
                })
            }
        };

        if(this.props.refresh !== nextProps.refresh){
            var data = nextProps.refresh.Item.SubCampaign;
            var discovery = Object.assign({},this.state.discoveryrequest);
            discovery.subCampaign.map((v:any,k:any) => {
                if (v.subCampaignName === data.subCampaignName){
                    v[k] = data
                    this.setState({
                        item : {
                            name : data.subCampaignName,
                            key : k 
                        },
                        activeIndex : {
                            index : k,
                            data : v
                        },
                        discoveryrequest : discovery
                    })
                }
            })
            // discovery.subCampaign.unshift(data);
            var dateArr = [nextProps.refresh.Item.StartDate,nextProps.refresh.Item.EndDate]
            var obj = {
                campaignName : nextProps.refresh.Item.CampaignName,
                campaignDate : dateArr,
                campaignID : nextProps.refresh.Item.CampaignID,
                subCampaign : data
            }
            this.setState({
                // activeIndex : {
                //     index : 0,
                //     data : data
                // },
                discoveryrequest : discovery
            },() =>{
                this.props.dispatch(discoveryAction(obj));
                this.props.dispatch(broadCastDataAction(this.state.discoveryrequest.campaignID));
            })   
        };

        if(this.props.forecastData !== nextProps.forecastData){
            // console.log("discovery",this.state.discoveryrequest)
            // console.log("forecastData",nextProps.forecastData)
            var data = nextProps.forecastData.Item.SubCampaign;
            // console.log(data)
            var discovery = Object.assign({},this.state.discoveryrequest);
            discovery.subCampaign.map((v:any,k:any) => {
                if (v.subCampaignName === data.subCampaignName){
                    v[k] = data
                    this.setState({
                        item : {
                            name : data.subCampaignName,
                            key : k 
                        },
                        activeIndex : {
                            index : k,
                            data : v
                        },
                        discoveryrequest : discovery
                    })
                }
            })
            // this.setState({
            //     discoveryrequest : discovery
            // })
        }

        if(this.props.discoveryRequest !== nextProps.discoveryRequest) {
            const output = JSON.parse(nextProps.discoveryRequest.output);
            const { targets } = output;
            const pool = targets[0].totalHouseholdPoolsize;
            
            const { discoveryrequest, activeIndex } = this.state;
            const { subCampaign } = discoveryrequest;
            (subCampaign[activeIndex.index] as any).pool = pool;
            (subCampaign[activeIndex.index] as any).timeStamp = nextProps.discoveryRequest.time;

            const newDiscoveryRequest = {
                ...discoveryrequest,
                subCampaign
            }
            // console.log("jj",nextProps.discoveryRequest)
            this.setState({
                discoveryrequest: newDiscoveryRequest,
                graphValue : pool,
                timeStamp : nextProps.discoveryRequest.time
            },() => {
                this.props.dispatch(broadCastDataAction(this.state.discoveryrequest.campaignID));
            })
        };
    }

    logout = () => {
        this.setState({
            visible : true,
        })
    }

    handleOk = (e:any) => {
        this.setState({
          visible: false,
        });
        this.props.history.push({
            pathname : "/landing"
        })
        window.location.hash="no-back-button";
        window.location.hash="Again-No-back-button";//again because google chrome don't insert first hash into history
        window.onhashchange=function(){window.location.hash="no-back-button";}
    };

    handleCancel = () => {
        this.setState({
            visible: false,
        });
    };

    handleBookCampaign = () => {
        this.setState({
            bookCampaign : false,
        });
        this.props.dispatch(BookCampaignAction(this.state.discoveryrequest));
        // console.log("booked",this.state.discoveryrequest)
    }

    handleCancelCampaign = () => {
        this.setState({
            bookCampaign : false,
        });
    }

    date = (value:any) => {
        var data = Object.assign({},this.state.discoveryrequest);
        data.campaignDate = value
        this.setState({
            discoveryrequest : data
        },() => {
            
        })
    }

    subCampaignDate = (value:any) => {
        const index = this.state.activeIndex.index;
        const {discoveryrequest} = this.state;
        const campaign = discoveryrequest.subCampaign[index];
        campaign.subCampaignDate = value
        this.setState({
           discoveryrequest : Object.assign({}, discoveryrequest),
           activeIndex: {
               index: index,
               data: campaign
           }
        })
    }

    onChange = (type:string,value:any) => {
        var data: any = Object.assign({},this.state.discoveryrequest)
        data[type] = value
        this.setState({
            discoveryrequest : data
        })
    }

    onSubNameChange = (e:any) => {
        const index = this.state.activeIndex.index;
        const {discoveryrequest} = this.state;
        const campaign = discoveryrequest.subCampaign[index];
        campaign.subCampaignName = e.target.value
        this.setState({
           discoveryrequest : Object.assign({}, discoveryrequest),
           activeIndex: {
               index: index,
               data: campaign
           }
        })
    }

    fillFields = () => {
        var obj = {
            type : "Type",
            target : "Target"
        }
        const index = this.state.activeIndex.index;
        const discoveryrequest = this.state.discoveryrequest;
        const campaign = discoveryrequest.subCampaign[index];

        const newCamp = this.updateAudienceTargetting(campaign, obj, null);

        discoveryrequest.subCampaign[index] = Object.assign({}, newCamp);
        
        this.setState({
            discoveryrequest
        })
    }

    updateAudienceTargetting = (campaign: any, value: any, key: any = null) => {
        const fieldsArr = campaign.audienceTargeting;
        var arr: any = Object.assign([], fieldsArr);

        if(key === null && fieldsArr.length < 6) {
            arr.push(value);
        } else if( key !== null) {
            arr[key] = Object.assign({}, value);
        } else {
            return campaign
        }

        campaign.audienceTargeting = Object.assign([], arr);

        return campaign;
    }

    deleteFieldRow = (k:any) => {
        const index = this.state.activeIndex.index;
        const discoveryrequest = this.state.discoveryrequest;
        const campaign = discoveryrequest.subCampaign[index];

        campaign.audienceTargeting.splice(k,1);

        discoveryrequest.subCampaign[index] = Object.assign({}, campaign);
        
        this.setState({
            discoveryrequest
        })
    }

    dropdownList = () => { 
        const index = this.state.activeIndex.index;
        const discoveryrequest = this.state.discoveryrequest;
        const campaign = discoveryrequest.subCampaign[index];
        let list = Object.assign([],this.state.dropdownTypeList);
        campaign.audienceTargeting.map((v:any,k:any) => {
            list.map((val:any, key:any) => {
                if(v.type === val){
                    list.splice(key, 1)
                }
            })
        })

        return list;
    }

    selectlist = (k:any,value:any) => {
        const index = this.state.activeIndex.index;
        const discoveryrequest = this.state.discoveryrequest;
        const campaign = discoveryrequest.subCampaign[index];

        const obj: any = campaign.audienceTargeting[k];

        if(obj.type !== "Type") {
            obj.target = "Target";            
        }

        obj.type = value;

        const newCamp = this.updateAudienceTargetting(campaign, obj, k);

        discoveryrequest.subCampaign[index] = Object.assign({}, newCamp);
        
        this.setState({
            discoveryrequest
        })
    }

    targetList = (k:any) => {
        const index = this.state.activeIndex.index;
        const discoveryrequest = this.state.discoveryrequest;
        const campaign = discoveryrequest.subCampaign[index];

        var arr: any = Object.assign([],campaign.audienceTargeting)
        switch(arr[k].type){
            case "Gender":
                return this.state.genderList;
                break;
            case "Age Range":
                return this.state.age;
               
            case "Age of Oldest Child":
                return this.state.ageOfOldest;
            
            case "Geotargeting":
                return this.state.geoTargettingList;
               
            case "Law Restrictions":
                return this.state.LawRestrictionsList;
               
            case "Household Income":
                return this.state.houseHoldList;  

            default:
               return "Select Type first"
        }
    }

    selectTargetlist = (k:any, value:any) => {
        const index = this.state.activeIndex.index;
        const discoveryrequest = this.state.discoveryrequest;
        const campaign = discoveryrequest.subCampaign[index];

        const obj: any = campaign.audienceTargeting[k];
        obj.target = value;

        const newCamp = this.updateAudienceTargetting(campaign, obj, k);

        discoveryrequest.subCampaign[index] = Object.assign({}, newCamp);
        
        this.setState({
            discoveryrequest
        })
    }

    addSubCampaign = () => {
        const { discoveryrequest } = this.state; 
        const key = "Sub Campaign "+(Object.keys(discoveryrequest.subCampaign).length + 1);
        var obj = {
            requestID : "",
            subCampaignName: "",
            adID : "null",
            subCampaignDate : "null",
            audienceTargeting : []
        }
        discoveryrequest.subCampaign.push(obj)
        this.setState({
            discoveryrequest : discoveryrequest
        }) 
        this.props.dispatch(subCampaignRequestIDAction())
    }

    deleteCampaign = () => {
        var { discoveryrequest } = this.state
        var index = this.state.activeIndex.index
        discoveryrequest.subCampaign.splice(index,1)
        var discovery = Object.assign({},discoveryrequest)
        var activeIndex = Object.assign({},this.state.activeIndex.data)
        activeIndex = discovery.subCampaign[0]

        this.setState({
            discoveryrequest : discoveryrequest,
            activeIndex : {
                index : 0,
                data : activeIndex
            }
        })
    }

    saveCampaign = () => {
        var data = Object.assign([],this.state.discoveryrequest) 
        const index = this.state.activeIndex.index;
        const {discoveryrequest} = this.state;
        const campaign = discoveryrequest.subCampaign[index];
        var discoveryData = {
            campaignID : data.campaignID, 
            campaignName : data.campaignName,
            campaignDate : data.campaignDate,
            subCampaign : campaign
        }

        this.props.dispatch(saveAction(discoveryData))
        swal("Sub-Campaign Saved")
    }

    discoverCampaign = () => {
        var data = Object.assign([],this.state.discoveryrequest) 
        const index = this.state.activeIndex.index;
        const {discoveryrequest} = this.state;
        const campaign = discoveryrequest.subCampaign[index];
        var discoveryData = {
            campaignID : data.campaignID, 
            campaignName : data.campaignName,
            campaignDate : data.campaignDate,
            subCampaign : campaign
        }

        this.props.dispatch(discoveryAction(discoveryData))
        swal("Discovered Sub-Campaign")
        // console.log("discovery",discoveryData)
    }

    refresh = () => {
        // console.log("kk",this.state.forecastData)
        var data = this.state.forecastData
        this.props.dispatch(refreshAction(data))
    }

    forecastData = (v:any) => {
        var data = JSON.parse(v.Discover);
        const { targets } = data;
        // console.log(v)
        const pool = targets[0].totalHouseholdPoolsize;
        this.setState({
            forecastData : v,
            graphValue : pool,
            timeStamp : v.Timestamp
        })
        // console.log("forecast",v)
        this.props.dispatch(getForecastDataAction(v))
    }

    bookCampaign = () => {
        this.setState({
            bookCampaign : true
        })
    }

    getCampaign = (name: string) => {
        const { subCampaign } = this.state.discoveryrequest;
        let c = null;
        let i = null;
        subCampaign.map((campaign: any, k: number) => {
            if(campaign.subCampaignName === name) {
                c = campaign;
                i = k;
            }
        })

        return {
            index: i,
            data: c
        };
    }

    timestamp = (time:any) => {
        var date = new Date(time * 1).toLocaleString();
        return date
    }

    options = () => {
        const options: Highcharts.Options = {
            credits: {
                enabled: false
            },
            
            chart: {
                borderColor: 'none',
                type: 'venn',
                borderWidth: 1,
                zoomType: 'xy',
                height: '27%',
            },
        
            legend: {
                enabled: false
            },
        
            title: {
                text: 'Sugar and fat intake per country',
                style : {
                    display : "none"
                }
            },
        
            subtitle: {
                style : {
                    display : "none"
                },
                text: 'Source: <a href="http://www.euromonitor.com/">Euromonitor</a> and <a href="https://data.oecd.org/">OECD</a>'
            },
        
            series: [{
                type: 'venn',
                name: '',
                data: [
                    {
                        sets: ['Pool size - '+ this.state.graphValue],
                        value: this.state.graphValue,
                        color : '#d4dae6'
                        
                    }, 
                ]
            }],
        };
        return options
    }

    loadChart = (props: HighchartsReact.Props) => {
        return(
            <div>
                <HighchartsReact
                    highcharts={Highcharts}
                    options={this.options()}
                    {...props}
                />
            </div>
        )
    }

    subCampaignDetails = (value: any) => {
        // console.log("value",value)
        const campaignData:any = this.getCampaign(value.name);
        // console.log("mmm",campaignData)
        this.setState({
            activeIndex : campaignData,
            item: value,
            timeStamp : campaignData.data.timestamp ? campaignData.data.timestamp : 0,
            graphValue : campaignData.data.pool ? campaignData.data.pool : 0,
        })
    }

    searchAddId = (name:any,e:any) => {
        
        const index = this.state.activeIndex.index;
        const {discoveryrequest} = this.state;
        const campaign = discoveryrequest.subCampaign[index];
        campaign.adID = e.target.value
        this.setState({
            discoveryrequest : Object.assign({}, discoveryrequest),
            activeIndex: {
                index: index,
                data: campaign,
            },
            searchDisplay : true
        })
        this.props.dispatch(searchAddIdAction(e.target.value))
    }

    getSearchData = (v:any) => {
        const index = this.state.activeIndex.index;
        const {discoveryrequest} = this.state;
        const campaign = discoveryrequest.subCampaign[index];
        campaign.adID = v
        this.setState({
            discoveryrequest : Object.assign({}, discoveryrequest),
            activeIndex: {
                index: index,
                data: campaign,
            },
            searchDisplay : false
        })
    }

    home = () => {
        this.props.history.push({
            pathname : "/dashboard"
        })
    }

    disableDate = (current:any) => {
        var discoveryrequest = this.state.discoveryrequest;
        var start = discoveryrequest.campaignDate;
        var startDate = start[0];
        var endDate = start[1];
        var s = startDate.split("-");
        var newStartDate = s[2]+"-"+s[1]+"-"+s[0];
        var e = endDate.split("-");
        var newEndDate = e[2]+"-"+e[1]+"-"+e[0];
        {
            if (current < moment(newStartDate)) {
                return true;
            } else if (current > moment(newEndDate)) {
                return true;
            } else {
                return false;
            }
        }
    }

    activeDiscoverButton = () => {
        var activeState = true;
        var data: any = Object.assign({},this.state.activeIndex.data);
        delete data["audienceTargeting"]
        Object.keys(data).map((v,k) => {
            if(data[v].length === 0 || data[v] === "null"){
                activeState = false;
            }
            return null;
        })
        return activeState
    }

    activeBookingButton = () => {
        var activeState = true;
        var data :any = this.state.discoveryrequest;
        delete data["audienceTargeting"];
        Object.keys(data).map((v,k) => {
            if(data[v].length === 0 || data[v] === "null"){
                activeState = false;
            }
            return null;
        })
        return activeState
    }

    // activeSaveButton = () => {
    //     var activeState = true;
    //     var data :any = this.state.discoveryrequest;
    //     var activeIndex = Object.assign({},this.state.activeIndex)
    //     var obj:any = {
    //         campaignName : data.campaignName ? data.campaignName : "",
    //         subCampaignName : activeIndex.data.subCampaignName ? activeIndex.data.subCampaignName : ""
    //     }
    //     Object.keys(obj).map((v,k) => {
    //         if(obj[v].length === 0){
    //             activeState = false;
    //         }
    //         return null;
    //     })
    //     return activeState
    // }

    render(){
        const { discoveryrequest } = this.state;
        var index  = this.state.activeIndex.index;
        var { audienceTargeting } = discoveryrequest.subCampaign[index];
        const horizontalStack = discoveryrequest.subCampaign.map((campaign:any,k:any) => {
            var obj = {
                name : campaign.subCampaignName,
                key: k
            }
            return obj;
        });
        var { activeIndex } = this.state;
        return(
            <div className = "dashboard-container">
                <div className = "dashboard-side-nav">
                    <div className = "dashboard-side-nav-logo">
                        <img src= {logo} alt ="" />
                    </div>
                    <div className = "dashboard-side-nav-home" onClick = {this.home.bind(this)}>
                        <img src= {active} alt ="" />
                    </div>
                    <div className = "dashboard-side-nav-logout" onClick = {this.logout.bind(this)}>
                        <img src= {logout} alt ="" />
                    </div>
                </div>
                <div className = "nb-layout">
                    <div className = "new-booking-upper-container">
                        <div className = "new-booking-bread-crum"><span><img src = {back} alt = "" style = {{cursor : "pointer"}} onClick = {this.home.bind(this)}/></span>New Campaign</div>
                        <div className = "new-booking-id-container">
                            <div className = "new-booking-header-wapper">
                                <div className = "new-booking-id">Request ID</div>
                                <div className = "new-booking-id" style = {{textOverflow: "ellipsis",overflow:"hidden"}}>{discoveryrequest.campaignID}</div>
                            </div>
                            <div style = {{flex:"3",paddingLeft:"50px"}}>
                                <Input
                                    value = {discoveryrequest.campaignName === "null" ? "" : discoveryrequest.campaignName}
                                    placeholder = "Name"
                                    onChange = {(e:any) => {
                                        this.onChange("campaignName",e.target.value)
                                    }}
                                >
                                </Input>
                            </div>
                            <div style = {{flex:"2.5",paddingLeft:"20px"}}>
                                <Dropdown
                                    value = "Date Range"
                                    type = "calender"
                                    selectedDate =  {this.date.bind(this)}
                                    placeholder = {['Date Range']}
                                    defaultValue = {discoveryrequest.campaignDate !== "null" && discoveryrequest.campaignDate[0] && discoveryrequest.campaignDate[0].length !== 0 ?
                                                [
                                                    moment(discoveryrequest.campaignDate[0], "DD/MM/YYYY"),
                                                    moment(discoveryrequest.campaignDate[1], "DD/MM/YYYY")
                                                ]
                                            :  [
                                                null,
                                                null
                                            ]
                                    }
                                    disableDate = {(current: any) => {
                                        const now: any = new Date();
                                        return current && current < now.setTime((new Date()).getTime() - 1*24*60*60*1000);
                                    }}
                                >
                                </Dropdown>
                            </div>
                        </div>
                    </div>
                    <div className = "new-booking-middle-container">
                        <div className = "nb-middle-container-up">
                            <div className = "nb-middle-left-container">
                                <div className = "nb-scroll">
                                    <div className = "nb-scroll-container">
                                        <Scroll
                                            arr = {horizontalStack}
                                            width = "470px"
                                            subCampaignDetails = {this.subCampaignDetails.bind(this)}
                                            item = {this.state.item}
                                        >
                                        </Scroll>
                                    </div>
                                    {
                                        this.activeDiscoverButton() ? 
                                        <div className = "nb-scroll-icon" onClick = {this.addSubCampaign.bind(this)}>
                                            <img src = {addIcon} alt = "" height = "30px" width = "30px" />
                                        </div> 
                                        :
                                        <div className = "nb-scroll-icon">
                                            <img src = {inActiveaddicon} alt = "" height = "30px" width = "30px" />
                                        </div> 
                                    }
                                        
                                </div>
                                
                                <div className = "nb-middle-header">
                                    <div className = "nb-middle-header-wrapper" style = {{marginLeft:"5px"}}>
                                        <div style = {{width:"50%"}}>
                                            <Input
                                                placeholder = "Name"
                                                onChange = {this.onSubNameChange.bind(this)}
                                                value = {activeIndex.data.subCampaignName}
                                            >
                                            </Input> 
                                        </div>
                                        <div className = "new-booking-header-wapper" style = {{paddingLeft:"20px",width:"40%"}}>
                                            <div className = "new-booking-id">Request ID</div>
                                            <div className = "new-booking-id" style = {{textOverflow: "ellipsis",overflow:"hidden"}}>{activeIndex.data.requestID}</div>
                                        </div>
                                        {
                                            discoveryrequest.subCampaign.length > 1 ?
                                            <div style = {{float:"right",paddingRight:"10px",cursor:"pointer"}} onClick = {this.deleteCampaign.bind(this)}><img src = {deleteActive} alt = ""/></div>
                                            : <div style = {{float:"right",paddingRight:"10px"}}><img src = {deleteInactive} alt = ""/></div>
                                        }
                                        
                                    </div>
                                    <div className = "nb-middle-header-wrapper">
                                        <div style = {{width:"50%"}}>
                                            <Input
                                                placeholder = "Creative ID"
                                                icon = {this.state.icon}
                                                onChange = {this.searchAddId.bind(this,"adID")}
                                                value = {activeIndex.data.adID === "null" ? "" : activeIndex.data.adID}
                                                style = {{
                                                    border: "solid 2px #979797",
                                                    borderRadius: "20px"
                                                }}
                                            >
                                            </Input>
                                            {
                                                this.state.searchDisplay ?  
                                                <div className = "searchlist">
                                                    {
                                                        this.state.searchList.map((v:any,k:any) => {
                                                            return(
                                                                <div key = {"addId"+k} className = "search-element" onClick = {this.getSearchData.bind(this,v)}>{v}</div>
                                                            )
                                                        }) 
                                                    }
                                                </div> : ""
                                            }
                                            
                                        </div>
                                        <div style = {{width:"43%",paddingLeft:"10px"}}>
                                            <Dropdown
                                                value = "Date Range"
                                                type = "calender"
                                                selectedDate =  {this.subCampaignDate.bind(this)}
                                                defaultValue = {
                                                    activeIndex.data.subCampaignDate !== "null" && activeIndex.data.subCampaignDate[0] &&  activeIndex.data.subCampaignDate[0].length !== 0 ?
                                                        [
                                                            moment(activeIndex.data.subCampaignDate[0], "DD/MM/YYYY"),
                                                            moment(activeIndex.data.subCampaignDate[1], "DD/MM/YYYY")
                                                        ]:
                                                        [
                                                            null,
                                                            null
                                                        ]
                                                }
                                                placeholder = {['Date Range']}
                                                disabled = {discoveryrequest.campaignDate === "null" || discoveryrequest.campaignDate[0] === "null" || discoveryrequest.campaignName === "null" || discoveryrequest.campaignName === ""? true : false}
                                                disableDate = {(current: any) => 
                                                    this.disableDate(current)
                                                }
                                            >
                                            </Dropdown>
                                        </div>
                                       
                                    </div>
                                    <div className = "audience-targetting-wrapper">
                                        <div className = "audience-targetting-element">Audience Targeting</div>
                                        {
                                            audienceTargeting.length < 6 ? 
                                            <div className = "audience-targetting-icon" onClick = {this.fillFields.bind(this)}>
                                                <img src = {addIcon} alt = "" height = "30px" width = "30px"/>
                                            </div>
                                            :
                                            <div className = "audience-targetting-icon" onClick = {this.fillFields.bind(this)}>
                                                <img src = {inActiveaddicon} alt = "" height = "30px" width = "30px"/>
                                            </div>
                                        }
                                        
                                    </div>
                                </div>
                                <div className = "nb-middle-body">
                                    {
                                        audienceTargeting ? audienceTargeting.map((v:any,k:any) => {
                                            return(
                                                <div className = "nb-middle-body-element-wrapper" key = {"audience"+k}>
                                                    <div className = "nb-middle-body-element">
                                                        <div>
                                                            <Dropdown
                                                                tagTitle = "Type"
                                                                value = {v.type}
                                                                type = "selectDropdown" 
                                                                dropdownList = {this.dropdownList()}
                                                                dropdownClick = {this.selectlist.bind(this,k)}
                                                            >
                                                            </Dropdown>
                                                        </div>
                                                    </div>
                                                    <div className = "nb-middle-body-element">
                                                        {v.type === "Type" ? 
                                                            <div className = "disable-dropdown">
                                                                type
                                                            </div>
                                                            : 
                                                        <div>
                                                            <Dropdown
                                                                tagTitle = "Target"
                                                                value = {v.target}
                                                                type = "selectDropdown" 
                                                                dropdownList = {this.targetList(k)}
                                                                dropdownClick = {this.selectTargetlist.bind(this,k)}
                                                            >
                                                            </Dropdown>
                                                        </div>
                                                        }
                                                    </div>
                                                    <div className = "nb-middle-body-element-icon" onClick = {this.deleteFieldRow.bind(this,k)}>
                                                        <img src = {deleteActive} alt = ""/>
                                                    </div>
                                                </div>
                                            )
                                        }) : null
                                    }
                                </div>
                                <div className = "nb-middle-footer">
                                <Button 
                                onClick = {this.saveCampaign.bind(this)}
                                style = {this.activeDiscoverButton() ? {
                                    width : "48%",
                                    height : "40px",
                                    borderRadius : "10px",
                                    fontSize  : "18px",
                                    border : "solid 2px #4c5057",
                                    background : "white",
                                    color : "#3c475c",
                                    marginLeft : "5px"
                                } : {
                                    width : "48%",
                                    height : "40px",
                                    borderRadius : "10px",
                                    fontSize  : "18px",
                                    border : "solid 2px #d8d8d8",
                                    background : "#d8d8d8",
                                    color : "white",
                                    marginLeft : "5px",
                                    pointerEvents : "none"
                                }}
                                >
                                    Save
                                </Button>

                                <Button 
                                onClick = {this.discoverCampaign.bind(this)}
                                style = {this.activeDiscoverButton() ? {
                                    width : "48%",
                                    height : "40px",
                                    borderRadius : "10px",
                                    fontSize  : "18px",
                                    marginLeft : "5px",
                                    background : "rgb(76, 80, 87)",
                                    color : "white"
                                }:{
                                    width : "48%",
                                    height : "40px",
                                    borderRadius : "10px",
                                    fontSize  : "18px",
                                    border : "solid 2px #d8d8d8",
                                    background : "#d8d8d8",
                                    color : "white",
                                    marginLeft : "5px",
                                    pointerEvents : "none"
                                }}
                                >
                                    Discover
                                </Button>
                                </div>
                            </div>
                            <div className = "nb-middle-right-container">
                                <div className = "nb-middle-right-upper">
                                    <div className = "nb-middle-right-footer-title">Discovery forecast</div>
                                    <div className = "nb-middle-right-broadcast-container">
                                        <div style = {{width:"55%",display:"flex",borderBottom:"2px solid #979797"}}>
                                            {
                                                this.state.broadCastArr.map((v:any,k:any) => {
                                                    return(
                                                        <div className = "nb-middle-right-broadcast" key = {"broadcast"+k} 
                                                        onClick = {this.forecastData.bind(this,v)}
                                                        >{k}</div>
                                                    )
                                                })
                                            }
                                        </div>
                                        {
                                            this.state.broadCastArr.length !==0 && this.state.graphValue !== 0?
                                            <div style = {{width:"10%",cursor:"pointer"}} onClick = {this.refresh}>
                                                <img src = {refreshicon} alt = ""/>
                                            </div> 
                                            : 
                                            <div style = {{width:"10%",cursor:"pointer"}}>
                                                <img src = {inActiverefreshicon} alt = ""/>
                                            </div> 
                                        }
                                        <div style = {{width:"35%",cursor:"pointer"}}>
                                            <div className = "nb-timestamp">Results from:</div>    
                                            <div className = "nb-timestamp">{this.state.timeStamp ? this.timestamp(this.state.timeStamp) : ""}</div>    
                                        </div>
                                        
                                    </div>
                                    <div className = "nb-middle-graph-container" >
                                        {
                                            this.state.graphValue === 0 ?
                                            <div className = "nb-middle-graph-container-element">
                                                "There is no data to display yet"
                                            </div> 
                                            :
                                            <HighchartsReact
                                                highcharts={Highcharts}
                                                options={this.options()}
                                            />
                                        }
                                        
                                    </div>
                                </div>
                                <div className = "nb-middle-right-footer">
                                    <div className = "nb-middle-right-footer-title">Booking Budget</div>
                                    <div className = "nb-middle-right-footer-elements">
                                        <div className = "nb-input-type-container">
                                                <Dropdown
                                                    tagTitle = "Priority Level"
                                                    value = {discoveryrequest.priorityLevel !== "null" ? discoveryrequest.priorityLevel : "Priority Level"}
                                                    type = "selectDropdown" 
                                                    dropdownList = {this.state.priorityList}
                                                    dropdownClick = {(value:any) => {
                                                        this.onChange("priorityLevel",value)  
                                                    }} 
                                                >
                                                </Dropdown>
                                        </div>
                                        <div className = "nb-input-type-container" style = {{paddingTop:"17px"}}>
                                            <Input
                                                type = "number"
                                                value = {discoveryrequest.CPM ? discoveryrequest.CPM : ""}
                                                placeholder = "CPM"
                                                onChange = {(e:any) => {
                                                    this.onChange("CPM",e.target.value)
                                                }}
                                            >
                                            </Input>
                                        </div>
                                    </div>
                                    <div className = "nb-middle-right-footer-elements">
                                        <div className = "nb-input-type-container">
                                            <Input
                                                type = "number"
                                                value = {discoveryrequest.budget ? discoveryrequest.budget : ""}
                                                placeholder = "Budget"
                                                onChange = {(e:any) => {
                                                    this.onChange("budget",e.target.value)
                                                }}
                                            >
                                            </Input>
                                        </div>
                                        <div className = "nb-input-type-container">
                                            <Input
                                                type = "number"
                                                value = {discoveryrequest.impressions ? discoveryrequest.impressions : ""}
                                                placeholder = "Number of Impressions"
                                                onChange = {(e:any) => {
                                                    this.onChange("impressions",e.target.value)
                                                }}
                                            >
                                            </Input>  
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className = "nb-middle-container-down">
                            <div className = "nb-button-container">
                            <Button 
                                onClick = {this.bookCampaign.bind(this)}
                                style = {this.activeBookingButton() ? {
                                    width : "309px",
                                    height : "40px",
                                    borderRadius : "10px",
                                    fontSize  : "18px",
                                    marginLeft : "5px",
                                    background : "#4c5057",
                                    color : "white",
                                }:{
                                    width : "309px",
                                    height : "40px",
                                    borderRadius : "10px",
                                    fontSize  : "18px",
                                    marginLeft : "5px",
                                    border : "solid 2px #d8d8d8",
                                    pointerEvents : "none",
                                    background : "#d8d8d8",
                                    color : "white",
                                }}
                                >
                                    Book Campaign
                                </Button>
                            </div>
                        </div>
                    </div> 
                </div>
                <Modal
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    >
                    <p>Are you sure you want to signout ?</p>
                </Modal>
                <Modal
                    visible={this.state.bookCampaign}
                    onOk={this.handleBookCampaign}
                    onCancel={this.handleCancelCampaign}
                    >
                    <p>Are you sure that you want to book this campaign? </p>
                </Modal>
            </div>
        )
    }
}