import React from 'react'
import "./index.css"
import styled from "styled-components"
import calender from "./../../../../../../img/calendar.svg"
import arrow from "./../../../../../../img/drop-arrow.svg"
import Calender from "./../calender/Calender"
import cross from "./../../../../../../img/x.svg"
import { DatePicker } from 'antd';
import moment from 'moment'

//interfaces//
import { StoreType } from "./../../../../../common/resources/redux/types"
import { DropdownStoreProps,DropdownProps } from "./Dropdown.interfaces"

export const DropdownContainer = styled.div`
    height: 40px;
    border-radius: 20px;
    border: solid 2px #979797;
    display: table;
    margin-left : 5px;
    flex : 2
`

export default class Dropdown extends React.Component<DropdownStoreProps>{

    _element = React.createRef();

    state = {
        calenderId : "month_swap",
        type : this.props.type ? this.props.type : "dropdown",
        element : this.props.element ? this.props.element : "select..",
        dropdownOpen : false,
        calendarOpen : false,
        dropdownList : this.props.dropdownList || this.props.dropdownList !== undefined ? this.props.dropdownList : ["Discovery Request","Booked","On-air","Completed","Canceled"],
        value : this.props.value ? this.props.value : "Target",
        dates : true,
        tagTitle : this.props.tagTitle ? this.props.tagTitle : "Tag",
        disableDate : this.props.disableDate ? this.props.disableDate : () => {},
        disabled : this.props.disabled ? this.props.disabled : false,
        defaultValue : this.props.defaultValue ? this.props.defaultValue : ""
    };

    componentWillReceiveProps(nextProps:any){
        if(this.state.value !== nextProps.value){
            this.setState({
                value : nextProps.value
            })
        };

        if(this.state.dropdownList !== nextProps.dropdownList){
            this.setState({
                dropdownList : nextProps.dropdownList
            })
        }

        if(this.state.disabled !== nextProps.disabled){
            this.setState({
                disabled : nextProps.disabled
            })
        }
    };

    openCalender = () => {
        this.setState({
            calendarOpen : true,
            calenderId : "month_swap"
        },() => {
            if (this.state.calendarOpen) {
                document.addEventListener('click', this.removeCalendar)
            }
        });
    };

    removeCalendar = (e:any) => {
        if(this.state.calendarOpen && this.state.calenderId !== e.target.id){
            this.setState({
                calendarOpen : false
            },() => {
                document.removeEventListener('click',this.removeCalendar)
            });
        };
    };

    openDropdown = () => {
        this.setState({
            dropdownOpen : true
            
        },() => {
            if (this.state.dropdownOpen) {
                document.addEventListener('click', this.removeDropdown)
            };
        }) ; 
    };

    removeDropdown = () => {
        if(this.state.dropdownOpen){
            this.setState({
                dropdownOpen : false
            },() => {
                document.removeEventListener('click',this.removeDropdown)
            });
        };
    };

    value(v : any){
        this.setState({
            value : v
        });
        this.props.dropdownClick(v);
    };

    reloadData = () => {
        this.props.reload()
        this.setState({
            // value : "Select",
            calendarOpen : true
        });
    };

    calenderId  = (id:any) => {
        this.setState({
            calenderId : id
        });
    };

    onselect(date:any){
        this.props.selectedDate(date);
        this.setState({
            value : date,
            calendarOpen : false
        });
    };

    dateChange = (date:any, dateString:any) => {
        this.props.selectedDate(dateString);
    };

    render(): React.ReactNode {
        const { RangePicker } = DatePicker
        const dateFormat = 'MM/DD/YYYY';
        let dropDownContainerClass = "ct-dropdown-close";
        if(this.state.value === "Type" || 
        this.state.value === "Status" || 
        this.state.value === "Priority Level" || 
        this.state.value === "Choose" || 
        this.state.value === "Law Restrictions" || 
        this.state.value === "Add ID" || 
        this.state.value === "Gender" || 
        this.state.value === "Household" || 
        this.state.value === "Age of Oldest Child" || 
        this.state.value === "Geotargeting" || 
        this.state.value === "Law Restrictions" || 
        this.state.value === "Start Date" || 
        this.state.value === "End Date" ||
        this.state.value === "Date Range"){
            dropDownContainerClass+= " background-change";
        } 
        const el: any = this.refs["drop_wrapper_"+this.state.value];
        let drownDownWidth = 0;
        if (this.state.dropdownOpen) {
            dropDownContainerClass+= " ct-dropdown-open";

            if (el) {
                drownDownWidth = el.offsetWidth
            };
        };

        let dropDownContainerSelectClass = "ct-dropdown-close"
        if (this.state.dropdownOpen) {
            dropDownContainerSelectClass+= " ct-dropdown-open";

            if (el) {
                drownDownWidth = el.offsetWidth
            };
        };
    
        return (
            <> 
                {
                    this.state.type === "calender" ? 
                        <div className = {dropDownContainerClass}>
                            <RangePicker
                                disabledDate = {this.state.disableDate}
                                placeholder = {this.props.placeholder}
                                disabled = {this.state.disabled}
                                value = {this.props.defaultValue}
                                onChange={this.dateChange}
                                format = "DD-MM-YYYY"
                            />
                        </div>
                    : 
                        this.state.type === "datePicker" ? 
                        <div className = {dropDownContainerClass} onClick = {this.openCalender.bind(this)}>
                            <div className = "dropdown-wrap" ref = {"drop_wrapper_"+this.state.value}>
                                <div className = "input-type-tag">{this.state.value}</div>
                                <div className = "input-type-icon">
                                    <img src = {calender} alt ="" 
                                        style={{
                                            paddingRight:"8px",
                                        }}
                                    /> 
                                </div>
                            </div>
                            <DropdownWrapper dropdown = {this.state.calendarOpen} width = {drownDownWidth}>
                                {
                                    this.state.calendarOpen ? 
                                        <Calender
                                            calenderId = { this.calenderId.bind(this) }
                                            onSelect = {this.onselect.bind(this)}
                                        >
                                        </Calender>
                                    : null
                                }
                            </DropdownWrapper>
                        </div>
                    :   
                        this.state.type === "selectDropdown" ? 

                        <React.Fragment>
                            <div className = {this.state.tagTitle !== this.state.value ? "dropdowntitle-show" : "dropdowntitle-hide" }>{this.state.tagTitle}</div>

                            <div className = {dropDownContainerSelectClass} style = {{backgroundColor:"white"}}>
                                <div className = "dropdown-wrap" ref = {"drop_wrapper_"+this.state.value}>
                                    <div className = "input-type-tag">{this.state.value}</div>
                                    <div className = "input-type-icon">
                                        <img src = {arrow} alt ="" 
                                            onClick = {this.openDropdown.bind(this)}
                                            style={{
                                                paddingRight:"8px",
                                                transform: this.state.dropdownOpen ? 'rotate(180deg)' : 'rotate(360deg)',
                                                transition: "transform 500ms"
                                            }}
                                        /> 
                                    </div>
                                </div>
                                <DropdownWrapper dropdown = {this.state.dropdownOpen} width = {drownDownWidth}>
                                {
                                    this.state.dropdownOpen ? this.state.dropdownList.map((v:any,k:any) => {
                                        return(
                                            <div key = {"dropdown"+k} className = "dropdown-element" onClick = {this.value.bind(this,v)}>{v}</div>
                                        )
                                    }) : null
                                }
                                </DropdownWrapper>
                            </div> 
                        </React.Fragment>
                        :
                        <div className = {dropDownContainerClass}>
                            <div className = "dropdown-wrap" ref = {"drop_wrapper_"+this.state.value}>
                                <div className = "input-type-tag">{this.state.value}</div>
                                <div className = "input-type-icon">
                                    {
                                        this.state.value === "Type" || this.state.value === "Status" || this.state.value === "Priority Level" || this.state.value === "Choose" ? 

                                        <img src = {arrow} alt ="" 
                                            onClick = {this.openDropdown.bind(this)}
                                            style={{
                                                paddingRight:"8px",
                                                transform: this.state.dropdownOpen ? 'rotate(180deg)' : 'rotate(360deg)',
                                                transition: "transform 500ms"
                                            }}
                                        /> : <img src = {cross} alt = "" height = "15px" onClick = {this.reloadData.bind(this)} style = {{paddingRight:"8px"}}/>
                                    }
                                    
                                </div>
                            </div>
                            <DropdownWrapper dropdown = {this.state.dropdownOpen} width = {drownDownWidth}>
                            {
                                this.state.dropdownOpen ? this.state.dropdownList.map((v:any,k:any) => {
                                    return(
                                        <div key = {"dropdown"+k} className = "dropdown-element" onClick = {this.value.bind(this,v)}>{v}</div>
                                    )
                                }) : null
                            }
                            </DropdownWrapper>
                        </div>   
                }    
            </>
        )
    }
}

const DropdownWrapper = styled.div<any>`
    position: absolute;
    margin-top: 32px;
    margin-left: -2.2px;
    background-color: #ffffff;
    z-index: 9;
    max-height: 240px;
    overflow-Y:auto;
    width: ${(props) => (props.width+9)+"px"};
    // ${(props) => props.dropdown ? "box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);": null }
    ${(props) => props.dropdown ? "border: solid #979797;": null }
    ${(props) => props.dropdown ? "border-width: 0px 2px 2px 2px": null }
    ${(props) => props.dropdown ? "border-radius: 0px 0px 20px 20px;": null }
`;