import React from "react";
import "./index.css";
import Button from "../../core/components/TestComponent/components/Button";
import { connect } from "./../../../common/ReduxDecorator";
import Scroll from "./../../core/components/Master/HorizontalScrollableStack";
import cross from "./../../../../img/x.svg";
import canceled from "./../../../../img/canceled.svg";
import Pending from "./../../../../img/pending.svg";
import Running from "./../../../../img/running.svg";
import Complete from "./../../../../img/complete.svg";
import high from "./../../../../img/high.svg";
import low from "./../../../../img/low.svg";
import medium from "./../../../../img/medium.svg";
import highProgress from "./../../../../img/highprogress.svg";
import lowProgress from "./../../../../img/lowprogress.svg";
import mediumProgress from "./../../../../img/mediumprogress.svg";

//interfaces
import { StoreType } from "./../../../common/resources/redux/types";
import {DiscoveryStoreProps, DiscoveryProps} from "./Discovery.interfaces";

@connect(
    (store:StoreType):DiscoveryStoreProps => {
        if(!store) return ({} as unknown) as DiscoveryStoreProps;

        return {
            bookingData : store.dashboard.bookingDetail,
        }
    }
)

export default class Discovery extends React.Component<DiscoveryStoreProps> {

    state: any = {
        activeSubCompaign: {
            index: -1,
            data: {}
        },
        item : {
            name: "SubCampaign 1",
            key: 0
        },
        discoveryData : {}
    };

    componentDidMount() {
        const { bookingData } = this.props;
        if(bookingData.subCampaign) {
            const item = bookingData.subCampaign;
            this.setState({
                activeSubCompaign: {
                    index: 0,
                    data: item[0]
                },
                item : {
                    name: item[0].subCampaignName,
                    key: 0
                },
                discoveryData : bookingData
            });
        };
    };

    componentWillReceiveProps(nextProps:any){
        if(this.props.bookingData !== nextProps.bookingData){
            const { bookingData } = nextProps;
            const item = bookingData.subCampaign;
            this.setState({
                activeSubCompaign: {
                    index: 0,
                    data: item[0]
                },
                item : {
                    name: item[0].subCampaignName,
                    key: 0
                },
                discoveryData : bookingData
            });
        };
    };

    // viewReport = () => {
        
    // };

    duplicate = () => {
        this.props.duplicate(this.state.discoveryData)
    }

    delete = () => {
        this.props.delete(this.state.activeSubCompaign.data.Id) 
    }

    edit = () => {
        this.props.edit(this.state.discoveryData) 
    }

    viewCamapign = (value: any) => {
        let data = {};
        const { bookingData } = this.props;
        const item = bookingData.subCampaign;

        item.map((subCampaign: any, k: number) => {
            if (value.key === k) {
                data = subCampaign;
            }
        });

        this.setState({
            activeSubCompaign: {
                index: value.key,
                data: data
            },
            item: value
        })
    };

    render(){
        const { discoveryData } = this.state;
        const { bookingData } = this.props;
        const { activeSubCompaign } = this.state;
        const item = bookingData.subCampaign;
        let horizontalStack = [];

        if (item) {
            horizontalStack = item.map((campaign: any, k: number) => {
                var obj = {
                    name : campaign.subCampaignName,
                    key : k
                }
                return obj;
            });
        };
        if(item === undefined ) return null;
        if(activeSubCompaign.data.audienceTargeting === undefined) return null;
        return(
            <>
                <div className = "discovery-container">
                    <div className = "cross" onClick = {this.props.closeSidebar}>
                        <img src = {cross} alt = "" />
                    </div>
                    <div className = "discovery-wrapper" style = {{marginTop : "40px",height : "166px"}}>
                        <div className = "discovery-title">{discoveryData.type}</div>
                       
                        <div className = "discovery-details-wrap">
                            <div className = "discovery-details-items">
                                <div className = "discovery-detail-title">Request ID</div>
                                <div className = "discovery-detail" style = {{width:"200px"}}>{discoveryData.campaignID}</div>
                            </div>
                            <div className = "discovery-details-items">
                                <div className = "discovery-detail-title">Dates</div>
                                <div className = "discovery-detail">{discoveryData.startDate+" - "+discoveryData.endDate || "DateRange"}</div>
                            </div>
                        </div>
                    </div>
                    <div className = "discovery-sub-campaign-container" style = {{marginTop:"10px"}}>
                        <Scroll
                            arr = {horizontalStack}
                            subCampaignDetails = {this.viewCamapign.bind(this)}
                            width = "553px"
                            item = {this.state.item}
                        >
                        </Scroll>
                    </div>
                    <div className = "discovery-wrapper" style = {{height : "",marginTop : "0px"}}>
                        <div className = "discovery-details-wrap" style = {{paddingTop : "15px"}}>
                            <div className = "discovery-details-items">
                                <div className = "discovery-detail-title">Request ID</div>
                                <div className = "discovery-detail" style = {{width:"200px"}}>{activeSubCompaign.data.requestID || "RequestID"}</div>
                            </div>
                            <div className = "discovery-details-items">
                                <div className = "discovery-detail-title">Add ID</div>
                                <div className = "discovery-detail">{activeSubCompaign.data.adID || "-"}</div>
                            </div>
                            <div className = "discovery-details-items">
                                <div className = "discovery-detail-title">Dates</div>
                                <div className = "discovery-detail">{activeSubCompaign.data.subCampaignStartDate+" - "+activeSubCompaign.data.subCampaignEndDate || "DateRange"}</div>
                            </div> 
                        </div>
                        <div className = "discovery-margin">Audience Targeting</div>
                        <div className = "br"></div>
                        <div className = "discovery-details-wrap" style = {{paddingTop:"0px"}}>
                            {
                                activeSubCompaign.data.audienceTargeting.map((v:any,k:any) => {
                                    return(
                                        <React.Fragment key = {"audience"+k}>
                                            <div className = "discovery-details-items-audience" style = {{}}>
                                                <div className = "discovery-detail-title-Audience">{v.type}</div>
                                                <div className = "discovery-detail">{v.target}</div>
                                            </div>
                                        </React.Fragment>
                                    )
                                })
                            }  
                        </div>
                        <div className = "discovery-margin">Discovery Results</div>
                        <div className = "br"></div>
                        <div className = "discovery-margin">
                            <div className = "discovery-detail-title">Pool</div>
                            <div className = "discovery-detail">{activeSubCompaign.data.pool}</div>
                        </div>
                    </div>
                    {
                        discoveryData.type === "Booked" ? 
                            <div className = "discovery-wrapper" style = {{height : "192px"}}>
                                <div className = "discovery-margin" style = {{paddingTop : "5px"}}>Budget</div>
                                <div className = "br"></div>
                                <div className = "discovery-details-wrap" style = {{paddingTop : "20px"}}>
                                    <div className = "discovery-details-items">
                                        <div className = "discovery-detail-title">CPM</div>
                                        <div className = "discovery-detail">{discoveryData.CPM}</div>
                                    </div>    
                                    <div className = "discovery-details-items">
                                        <div className = "discovery-detail-title">Budget</div>
                                        <div className = "discovery-detail">{discoveryData.budget}</div>
                                    </div>
                                    <div className = "discovery-details-items">
                                        <div className = "discovery-detail-title">Number of Impressions</div>
                                        <div className = "discovery-detail">{discoveryData.impressions}</div>
                                    </div>
                                </div>
                                <div className = "discovery-details-wrap" style = {{paddingTop : "20px"}}>
                                    <div className = "discovery-details-items">
                                        <div className = "discovery-detail-title">Booking</div>
                                        <div className = "discovery-detail">
                                            <span style = {{paddingRight:"5px"}}>  
                                                <img src = {
                                                discoveryData.status === "Canceled" ?
                                                canceled :
                                                    discoveryData.status === "Pending"
                                                        ? Pending : 
                                                            discoveryData.status === "Running"
                                                                ? Running :
                                                                    discoveryData.status === "Complete" 
                                                                        ? Complete : "" } alt = "" />
                                                
                                            </span>
                                            {discoveryData.status}</div>
                                    </div>
                                    <div className = "discovery-details-items">
                                        <div className = "discovery-detail-title">Priority Level</div>
                                        <div className = "discovery-detail">
                                            <span style = {{paddingRight:"5px"}}>
                                                <img src = {
                                                
                                                  discoveryData.priorityLevel === "High" ? high : discoveryData.priorityLevel === "Medium" ? medium : discoveryData.priorityLevel === "Low" ? low : ""
                                                } alt = "" />
                                            </span>
                                            {discoveryData.priorityLevel}</div>
                                    </div>
                                    <div className = "discovery-details-items">
                                        <div className = "discovery-detail-title">Delivery</div>
                                        <div className = "discovery-detail">
                                            <span>
                                                <img src = 
                                                {
                                                   discoveryData.DeliveryStatus === "Bad" ? 
                                                    lowProgress : 
                                                       discoveryData.DeliveryStatus === "Moderate" 
                                                            ? mediumProgress : 
                                                               discoveryData.DeliveryStatus === "highProgress"
                                                                    ? highProgress :
                                                                        ""
                                                }
                                                alt = "" />
                                            </span>
                                            {discoveryData.DeliveryStatus}
                                        </div>
                                    </div>
                                </div>
                            </div>  
                        : ""
                    }
                    
                    <div className = "discovery-button-wrapper">
                        <Button
                            onClick = {this.duplicate.bind(this)}
                            style = {{
                                width : "170px",
                                height : "40px",
                                borderRadius : "10px",
                                border: "solid 2px #2d2d2d",
                                backgroundColor : "white",
                                fontSize  : "18px",
                                color: "#2d2d2d",
                                marginRight : "5px"
                            }}
                        >
                            Duplicate
                        </Button>
                        {
                            discoveryData.type === "Booked" ? 
                                <div></div>
                                // <Button
                                //     onClick = {this.viewReport.bind(this)}
                                //     style = {{
                                //         width : "170px",
                                //         height : "40px",
                                //         borderRadius : "10px",
                                //         fontSize  : "18px",
                                //     }}
                                // >
                                //     View Report
                                // </Button>
                            :
                                <React.Fragment>
                                    <Button
                                        onClick = {this.delete.bind(this)}
                                        style = {{
                                            width : "170px",
                                            height : "40px",
                                            borderRadius : "10px",
                                            backgroundColor : "white",
                                            border: "solid 2px #2d2d2d",
                                            fontSize  : "18px",
                                            color: "#2d2d2d",
                                            marginRight : "5px"
                                        }}
                                    >
                                        Delete
                                    </Button>
                                    <Button
                                        onClick = {this.edit.bind(this)}
                                        style = {{
                                            width : "170px",
                                            height : "40px",
                                            borderRadius : "10px",
                                            fontSize  : "18px",
                                        }}
                                    >
                                        View Results
                                    </Button>
                                </React.Fragment>
                            
                        }
                    </div>
                </div>
                
            </>
        )
    }
}
