import React from "react"
import "./index.css"

//interfaces
import { Sidebar } from "./Sidebar.interfaces"

export default class SideBar extends React.Component<Sidebar>{
    
    state = {
        display : this.props.display ? this.props.display : null
    }

    componentWillReceiveProps(nextProps:any){
        if(this.props.display !== nextProps.display){
            this.setState({
                display : nextProps.display
            })
        }
    }

    render(){
        var className = "SideBar-container";
            
        if(this.state.display !== null && this.state.display)
            className += " SideBar-container-In";
        else if(this.state.display !== null && !this.state.display)
            className += " SideBar-container-out"; 

        return(
            <>
                <div
                    ref = "sidebar_component"
                    className = {className} style = {{overflowY:"auto"}}>
                    {this.props.children}
                </div>
            </>
        )
    }
}