import { Dictionary } from "../common/resources/lang/object/Dictionary";
import { ActionType } from "../common/resources/redux/types";

interface ReducerState {
  testData: Dictionary;
  counter: number;
  user : object;
  registerData  : object;
  profileData : object;
  updatedData : object;
  newData : object;
}

export default function reducer(
  state: ReducerState = {
    testData: {},
    counter: 0,
    user : {},
    registerData : {},
    profileData : {},
    updatedData : {},
    newData : {},
    
  },
  action: ActionType
): ReducerState {
  switch (action.type) {
    case "TEST_ACTION": {
      return {
        ...state,
        testData: action.payload.message,
        counter: action.payload.counter
      };
    }
    case "LOGIN_ACTION" : {
      return { ...state , user : action.payload}
    }

    case "REGISTRATION_ACTION" : {
      return { ...state , registerData : action.payload}
    }

    case "GET_PROFILE" : {
      return { ...state , profileData : action.payload }
    }

    case "ADD_PROFILE" : {
      return { ...state, newData : action.payload }
    }

    case "UPDATE_PROFILE" : {
      return { ...state, updatedData : action.payload }
    }
  }

  return state;
}
