import React from "react";
import "./index.css";
import { PageContainer } from "./../App/style/layout.styled";
import Button from "./../core/components/TestComponent/components/Button";
import Dropdown from "../core/components/Master/Dropdown";
import SideBar from "../core/components/Master/sideBar";
import Discovery from "./DiscoveryRequest/index";
import high from "./../../../img/high.svg";
import low from "./../../../img/low.svg";
import logo from "./../../../img/talpa-logo.svg";
import medium from "./../../../img/medium.svg";
import canceled from "./../../../img/canceled.svg";
import Pending from "./../../../img/pending.svg";
import Running from "./../../../img/running.svg";
import Complete from "./../../../img/complete.svg";
import highProgress from "./../../../img/highprogress.svg";
import lowProgress from "./../../../img/lowprogress.svg";
import mediumProgress from "./../../../img/mediumprogress.svg";
import active from "./../../../img/inactive.svg";
import logout from "./../../../img/log-out.svg";
import moment from "moment"
import { getRequestAction, getSearchRequestAction, getFilterdDataAction, getBookingDetailAction, requestIDAction, deleteSubCampaignAction, getToken } from "./../../actions/dashboardAction";
import { connect } from "./../../common/ReduxDecorator";
import ReactTooltip from 'react-tooltip';
import swal from 'sweetalert';
import { Modal } from 'antd';

//interfaces
import { StoreType } from "./../../common/resources/redux/types";
import {DashboardStoreProps, DashboardProps} from "./Dashboard.interfaces";

@connect(
    (store:StoreType):DashboardStoreProps => {
        if(!store) return ({} as unknown) as DashboardStoreProps;

        return {
            requestData : store.dashboard.requestData,
            requestId : store.dashboard.requestId,
            delete : store.dashboard.delete,
            token : store.dashboard.token
        }
    }
)

export default class Hello extends React.Component<DashboardProps>{

    state = {
        headerData : ["Type","Name","Request ID","Start Date","End Date","Delivery","Status","Priority"],
        priorityList : ["High","Medium","Low"],
        bookedStatusList : ["Canceled","Pending","Running","Complete"],
        statusList : ["Discovery Request","Booked"],
        requestList :[],
        display : false,
        filterObj : {
            type: "",
            bookedStatus:"",
            priorityLevel: "",
            startDate :"",
            endDate : ""
        },
        visible: false,
        valuetype : "Type",
        valuebooking : "Status",
        valuepriority : "Priority Level",
        duplicateCampaign : {
            campaignID : "null", 
            campaignName : "null",
            campaignDate : "null",
            subCampaign : [
                {
                    requestID : "null",
                    subCampaignName: "SubCampaign 1",
                    adID : "null",
                    subCampaignDate : "null",
                    audienceTargeting : []
                } 
           ],
           priorityLevel : "null",
           CPM : "null",
           budget : "null",
           impressions : "null"
        }
    };

    tooltip: any = {};

    componentDidMount(){
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]
        // this.props.dispatch(getToken(token))
        var value = "";
        this.props.dispatch(getRequestAction());
    };

    componentWillReceiveProps(nextProps:any){
        if(this.props.requestData !== nextProps.requestData){
            if(nextProps.requestData && nextProps.requestData.data){
                this.setState({
                    requestList : nextProps.requestData.data
                });
            };
        };

        if(this.props.requestId !== nextProps.requestId){
            var url_string = window.location.href;
            var url = new URL(url_string);
            var c:any = url.hash;
            var token = c.split(/[=&]/)[1]

            var discoveryRequest = Object.assign({},this.state.duplicateCampaign);
            discoveryRequest.campaignID = nextProps.requestId.requestID;
            this.setState({
                duplicateCampaign : discoveryRequest,
            },() => {
                this.props.history.push({
                    pathname : "/bookingModule",
                    state : {
                        data : this.state.duplicateCampaign,
                        // token : token
                    }
                });
            })
        };

        if(this.props.delete !== nextProps.delete){
            var url_string = window.location.href;
            var url = new URL(url_string);
            var c:any = url.hash;
            var token = c.split(/[=&]/)[1]
            this.props.dispatch(getRequestAction());
        }
    };

    search = (e:any) => {
        var value = e.target.value;
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]
        this.props.dispatch(getSearchRequestAction(value));
    };

    newCampaign = () => {
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]
        this.props.history.push({
            pathname : "/bookingModule",
            state : {
                // token : token
            }
        })
        this.props.dispatch(requestIDAction());
    };

    filter = (key:any,v:any) => {
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]

        if(key === "type"){
            this.setState({
                valuetype : v
            });
        }
        else if(key === "bookedStatus"){
            this.setState({
                valuebooking : v
            });
        }
        else if(key === "priorityLevel"){
            this.setState({
                valuepriority : v
            });
        }

        var obj:any = Object.assign({},this.state.filterObj);
        obj[key] = v
        this.setState({
            filterObj : obj
        },() => {
            // console.log("dash",this.state.filterObj)
            this.props.dispatch(getFilterdDataAction(this.state.filterObj.type,this.state.filterObj.bookedStatus,this.state.filterObj.priorityLevel,this.state.filterObj.startDate,this.state.filterObj.endDate));
        });

    };

    openRequestDetail = (v:any) => {
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]
        var id = v.Id;
        var requestID = v.RequestID;
        var type = v.Type
        this.setState({
            display : true
        });
        console.log("cc",v)
        this.props.dispatch((getBookingDetailAction(id,requestID,type)));
    };

    closeSidebar = () => {
        this.setState({
            display : false,
        });
    };

    onSearchFocus = (e: any) => {
        e.target.placeholder = "";
    };

    onSearchBlur = (e: any) => {
        e.target.placeholder = "Type to search";
    };

    reload = (key:any) => {
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]

        var obj:any = Object.assign({},this.state.filterObj);
        obj[key] = "";
        this.setState({
            filterObj : obj
        },() => {
            this.props.dispatch(getFilterdDataAction(this.state.filterObj.type,this.state.filterObj.bookedStatus,this.state.filterObj.priorityLevel,this.state.filterObj.startDate,this.state.filterObj.endDate));
        });

        if(key === "type"){
            this.setState({
                valuetype : "Type"
            });
        }
        else if(key === "bookedStatus"){
            this.setState({
                valuebooking : "Status"
            });
        }
        else if(key === "priorityLevel"){
            this.setState({
                valuepriority : "Priority Level"
            });
        }
    };

    logout = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = (e:any) => {
        this.setState({
          visible: false,
        });
        this.props.history.push({
            pathname : "/landing"
        })
        window.location.hash="no-back-button";
        window.location.hash="Again-No-back-button";//again because google chrome don't insert first hash into history
        window.onhashchange=function(){window.location.hash="no-back-button";}
    };
    
    handleCancel = (e:any) => {
        this.setState({
            visible: false,
        });
    };

    duplicate = (duplicateData : any) => {
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]
        this.setState({
            display : false,
            duplicateCampaign : duplicateData
        });
        this.props.dispatch(requestIDAction());
    }

    delete = (id:any) => {
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]
        this.setState({
            display : false,
        });
        this.props.dispatch(deleteSubCampaignAction(id))
        swal("Sub-Campaign Deleted")
    }
    edit = (editedData:any) => {
        var url_string = window.location.href;
        var url = new URL(url_string);
        var c:any = url.hash;
        var token = c.split(/[=&]/)[1]

        this.setState({
            display : false,
        });
        this.props.history.push({
            pathname : "/bookingModule",
            state : {
                data : editedData,
                // token : token
            }
        });
    }

    render(): React.ReactNode{
        return(
            <>
                <PageContainer>
                    <div className = "dashboard-container">
                        <div className = "dashboard-side-nav">
                            <div className = "dashboard-side-nav-logo">
                                <img src= {logo} alt ="" />
                            </div>
                            <div className = "dashboard-side-nav-home">
                                <img src= {active} alt ="" />
                            </div>
                            <div className = "dashboard-side-nav-logout" onClick = {this.logout.bind(this)}>
                                <img src= {logout} alt ="" />
                            </div>
                        </div>
                        <div className = "dashboard-layout">
                            <div className = "dashboard-button-wrapper">
                                <Button 
                                onClick = {this.newCampaign.bind(this)}
                                style = {{
                                    width : "193px",
                                    height : "40px",
                                    borderRadius : "10px",
                                    fontSize  : "18px"
                                }}
                                >
                                    New Campaign
                                </Button>
                            </div>
                            <div className = "dashboard-search-wrapper">

                                <input type = "search" 
                                    className = "dashboard-search"
                                    placeholder = "Type to search" 
                                    onChange = {this.search.bind(this)}
                                    onFocus = {this.onSearchFocus}
                                    onBlur = {this.onSearchBlur}
                                />
                            </div>  
                            <div className = "dashboard-inputType-wrapper">
                                <Dropdown
                                    dropdownList = {this.state.statusList}
                                    value = {this.state.valuetype}
                                    reload = {this.reload.bind(this,"type")}
                                    dropdownClick = {this.filter.bind(this,"type")}
                                >
                                </Dropdown>
                                <Dropdown 
                                    value = {this.state.valuebooking}
                                    reload = {this.reload.bind(this,"bookedStatus")}
                                    dropdownList = {this.state.bookedStatusList}
                                    dropdownClick = {this.filter.bind(this,"bookedStatus")}
                                >
                                </Dropdown>
                                <Dropdown 
                                    dropdownList = {this.state.priorityList}
                                    reload = {this.reload.bind(this,"priorityLevel")}
                                    dropdownClick = {this.filter.bind(this,"priorityLevel")}
                                    value = {this.state.valuepriority}
                                >
                                </Dropdown>
                                <Dropdown 
                                    type = "calender"
                                    value = "Start Date"
                                    reload = {this.reload.bind(this,"startDate")}
                                    selectedDate =  {this.filter.bind(this,"startDate")}
                                    defaultValue = {this.state.filterObj.startDate !== "null" && this.state.filterObj.startDate[0] && this.state.filterObj.startDate[0].length !== 0 ?
                                                [
                                                    moment(this.state.filterObj.startDate[0], "DD/MM/YYYY"),
                                                    moment(this.state.filterObj.startDate[1], "DD/MM/YYYY")
                                                ]
                                            :  [
                                                null,
                                                null
                                            ]
                                    }
                                    placeholder = {['Start Date']}
                                >
                                </Dropdown>
                                <Dropdown 
                                    type = "calender"
                                    value = "End Date"
                                    reload = {this.reload.bind(this,"endDate")}
                                    selectedDate =  {this.filter.bind(this,"endDate")}
                                    placeholder = {['End Date']}
                                    defaultValue = {this.state.filterObj.endDate !== "null" && this.state.filterObj.endDate[0] && this.state.filterObj.endDate[0].length !== 0 ?
                                                [
                                                    moment(this.state.filterObj.endDate[0], "DD/MM/YYYY"),
                                                    moment(this.state.filterObj.endDate[1], "DD/MM/YYYY")
                                                ]
                                            :  [
                                                null,
                                                null
                                            ]
                                    }
                                >
                                </Dropdown>
                            </div>
                            <div className = "dashboard-table-wrapper">
                                <div className = "dashboard-table-header-wrapper">
                                    {
                                        this.state.headerData.map((v,k) => {
                                            return(
                                                <div key = {"header"+k} className = "dashboard-table-header-cells">{v}</div>
                                            )
                                        })
                                    }
                                </div>
                                <div className = "dashboard-table-body-wrapper">
                                    {
                                        this.state.requestList.length !== 0 ? this.state.requestList.map((v:any,k:any) => {
                                            return(
                                                <div 
                                                    key = {"items"+k}
                                                    className = {
                                                        v.Type === "Discovery Request" ? 
                                                            "dashboard-table-items-wrapper-grey" : 
                                                                v.Type === "Booked" ? 
                                                                    "dashboard-table-items-wrapper-green" : 
                                                                        v.Type === "On-air" ? 
                                                                            "dashboard-table-items-wrapper-blue" :
                                                                                v.Type === "Complete" ? 
                                                                                    "dashboard-table-items-wrapper-blue" :
                                                                                        "dashboard-table-items-wrapper-red"
                                                    }
                                                    onClick = {this.openRequestDetail.bind(this,v)}
                                                >
                                                    <div className = "dashboard-table-cells">
                                                        {v.Type}
                                                    </div>
                                                    <div className = "dashboard-table-cells">
                                                        {v.Name}
                                                    </div>
                                                    <div className = "dashboard-table-cells">
                                                        {v.RequestID}
                                                    </div>
                                                    <div className = "dashboard-table-cells">
                                                        {v.StartDate}
                                                    </div>
                                                    <div className = "dashboard-table-cells">
                                                        {v.EndDate}
                                                    </div>
                                                    <div className = "dashboard-table-cells">
                                                        <img 
                                                            onMouseOver = {() => {
                                                                ReactTooltip.show(this.tooltip[v.DeliveryStatus+"_k_"+v.Id])
                                                            }}
                                                            onMouseOut = {() => {
                                                                ReactTooltip.hide(this.tooltip[v.DeliveryStatus+"_k_"+v.Id])
                                                            }}
                                                            src = {
                                                                v.DeliveryStatus === "Bad" ? 
                                                                    lowProgress : 
                                                                        v.DeliveryStatus === "Moderate" 
                                                                            ? mediumProgress : 
                                                                                v.DeliveryStatus === "highProgress"
                                                                                    ? highProgress :
                                                                                        ""
                                                                                
                                                            } alt = ""
                                                        />
                                                        <p ref={ref => {
                                                            this.tooltip[v.DeliveryStatus+"_k_"+v.Id] = ref;
                                                        }} data-tip={"Delivery: "+v.DeliveryStatus} style = {{marginTop: "-18px"}}></p>
                                                    </div>
                                                    <div className = "dashboard-table-cells">
                                                        <img 
                                                        onMouseOver = {() => {
                                                            ReactTooltip.show(this.tooltip[v.Status+"_k_"+v.Id])
                                                        }}
                                                        onMouseOut = {() => {
                                                            ReactTooltip.hide(this.tooltip[v.Status+"_k_"+v.Id])
                                                        }}
                                                        src = {
                                                            v.Status === "Canceled" ?
                                                                canceled :
                                                                    v.Status === "Pending"
                                                                        ? Pending : 
                                                                            v.Status === "Running"
                                                                                ? Running :
                                                                                    v.Status === "Complete" 
                                                                                        ? Complete : "" 

                                                        } alt = "" />
                                                        <p ref={ref => {
                                                            this.tooltip[v.Status+"_k_"+v.Id] = ref;
                                                        }} data-tip={"Status: "+v.Status} style = {{marginTop: "-18px"}}></p>
                                                    </div>
                                                    <div className = "dashboard-table-cells">
                                                        <img 
                                                            onMouseOver = {() => {
                                                                ReactTooltip.show(this.tooltip[v.PriorityLevel+"_k_"+v.Id])
                                                            }}
                                                            onMouseOut = {() => {
                                                                ReactTooltip.hide(this.tooltip[v.PriorityLevel+"_k_"+v.Id])
                                                            }}
                                                            src = {v.PriorityLevel === "High" ? high : v.PriorityLevel === "Medium" ? medium : v.PriorityLevel === "Low" ? low : ""} alt = "" 
                                                        />
                                                        <p ref={ref => {
                                                            this.tooltip[v.PriorityLevel+"_k_"+v.Id] = ref;
                                                        }} data-tip={"Priority: "+v.PriorityLevel} style = {{marginTop: "-18px"}}></p>
                                                    </div>
                                                </div>
                                            )
                                        }) :  
                                        <div className = "dashboard-empty-result">There is no data to display yet</div>
                                    } 
                                </div>
                            </div> 
                            <SideBar 
                                display = {this.state.display}
                            >
                                <Discovery
                                    closeSidebar = {this.closeSidebar.bind(this)}
                                    duplicate = {this.duplicate.bind(this)}
                                    delete = {this.delete.bind(this)}
                                    edit = {this.edit.bind(this)}
                                >
                                </Discovery>
                            </SideBar>
                        </div>
                    </div>
                    <ReactTooltip />
                    <Modal
                        // title="Logout"
                        visible={this.state.visible}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        >
                        <p>Are you sure you want to signout ?</p>
                    </Modal>
                </PageContainer>
            </>
        )
    }
} 